<template>
  <layout-default>
    <div class="card card-container">
      <h3 class="mb-4">Login</h3>
      <img
        id="profile-img"
        src="//ssl.gstatic.com/accounts/ui/avatar_2x.png"
        class="profile-img-card"
      />
      <form name="form" @submit.prevent="handleLogin">
        <div class="form-group">
          <label for="username">Username</label>
          <input
            v-model="user.username"
            v-validate="'required'"
            type="text"
            class="form-control"
            name="username"
          />
          <error-validation fieldName="username" :error="errors" />
        </div>
        <div class="form-group">
          <label for="password">Password</label>
          <input
            v-model="user.password"
            v-validate="'required'"
            type="password"
            class="form-control"
            name="password"
          />
          <error-validation fieldName="password" :error="errors" />
        </div>
        <div class="form-group">
          <button
            class="btn btn-primary btn-block"
            :disabled="errors.has('username') || errors.has('password')"
          >
            <span
              v-show="isLoading"
              class="spinner-border spinner-border-sm"
            ></span>
            <span> Login </span>
          </button>
        </div>
        <div class="form-group">
          <div v-if="getError" class="alert alert-danger" role="alert">
            {{ getError }}
          </div>
        </div>
      </form>
    </div>
  </layout-default>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import LayoutDefault from "@/layout/LayoutDefault.vue";
import ErrorValidation from "@/components/ErrorValidation.vue";

export default {
  components: { LayoutDefault, ErrorValidation },
  name: "Login",
  data() {
    return {
      user: {
        username: "",
        password: "",
      },
    };
  },
  computed: {
    ...mapGetters("loader", ["isLoading", "getError"]),
  },
  methods: {
    ...mapActions("loader", ["removeError"]),
    ...mapActions("user", ["login"]),

    async handleLogin() {
      const isValid = await this.$validator.validate();
      if (isValid) {
        await this.login(this.user);
        this.$router.push("/");
      }
    },
  },
};
</script>

<style scoped>
label {
  text-align: start;
  font-weight: bold;
  display: block;
  margin-top: 10px;
}

.card-container.card {
  max-width: 350px !important;
  padding: 40px 40px;
}

.card {
  background-color: #f7f7f7;
  padding: 20px 25px 30px;
  margin: 0 auto 25px;
  margin-top: 50px;
  -moz-border-radius: 2px;
  -webkit-border-radius: 2px;
  border-radius: 2px;
  -moz-box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
  -webkit-box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
}

.profile-img-card {
  width: 96px;
  height: 96px;
  margin: 0 auto 10px;
  display: block;
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
  border-radius: 50%;
}
</style>
